.tree-resize {
  min-height: 100vh;
  min-width: initital;
  max-width: 50%;
  background-color: #21262b;
  -webkit-user-select: none !important;
     -moz-user-select: none !important;
      -ms-user-select: none !important;
          user-select: none !important;
  -webkit-transition: linear 0.2s;
  -o-transition: linear 0.2s;
  transition: linear 0.2s;
}
.explorer {
  height: 100%;
}
.explorer .spinner {
  position: fixed;
  top: calc(50vh - 100px);
  left: calc(50vw - 100px);
}
.content {
  padding-left: 10px;
  width: 100%;
  overflow-y: auto;
}
.delete-dialog .rodal-dialog,
.rename-dialog .rodal-dialog {
  max-width: 90%;
}
.dl-view {
  width: 300px;
  max-width: 50%;
  min-width: 185px;
  position: fixed;
  bottom: 70px;
  right: 10px;
}
#convert-button {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
@media all and (max-width: 760px) {
  .tree-resize {
    display: none;
  }
}



