.pricing-paper {
  width: 30vw;
  min-width: 165px;
  max-height: 50vh;
  height: 350px;
  min-height: 265px;
  -webkit-transition: linear .1s;
  -o-transition: linear .1s;
  transition: linear .1s;
}
.pricing-paper .pricing-body {
  height: 100%;
  width: 100%;
  margin: 0;
}
.pricing-paper .pricing-body .pricing-component {
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.pricing-paper .pricing-body .pricing-component.icon {
  height: 33%;
}
.pricing-paper .pricing-body .pricing-component.icon svg {
  width: 100px;
  height: 100px;
}
.pricing-paper .pricing-body .pricing-component.title {
  padding: 15px 0;
  font-family: 'Lobster', cursive;
  font-size: 2rem;
}
.pricing-paper .pricing-body .pricing-component.price-tag {
  font-size: 33px;
  font-weight: bold;
}
.pricing-paper .pricing-body .pricing-component.price-tag:after {
  content: "/ mois";
  position: relative;
  margin-top: 22px;
  font-size: 8px;
}
.pricing-paper .pricing-body .pricing-component.description {
  padding: 15px 5px;
  color: #1c1c66;
  font-weight: bold;
}
.pricing-paper:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}
@media all and (max-width: 500px) {
  .pricing-paper {
    width: 90vw;
    height: 27%;
    min-height: 380px;
  }
  .pricing-paper .pricing-body .pricing-component.icon svg {
    width: 70px;
    height: 70px;
  }
  .pricing-paper .pricing-body .pricing-component.price-tag {
    font-size: 45px;
  }
}



