.nv-container {
  position: relative;
  height: 105vh;
  width: 100vw;
  padding-top: 10px;
  padding-left: 10px;
  padding-bottom: 35px;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.nv-container .dropzone {
  height: 500vh;
  width: 98vw;
}
.nv-container .dropzone .node {
  padding: 10px;
}
.nv-container .dropzone .dnd-dialog .rodal-dialog svg {
  width: 110px;
  height: 110px;
}
.nv-container .dropzone .nothing-here {
  color: #afafaf;
  text-align: center;
}
.nv-container .dropzone .nothing-here svg {
  height: 100%;
}
.disabled {
  background-color: #cbcbcb;
  color: #666666;
}
.react-contextmenu-item {
  z-index: 999;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  padding: 10px;
  width: 200px;
}
.react-contextmenu-item svg {
  padding-right: 5px;
}
.react-contextmenu-item.react-contextmenu-item--disabled {
  background-color: #cbcbcb;
  color: #666666;
}
.react-contextmenu-item.react-contextmenu-item--disabled:hover {
  cursor: auto;
  background-color: #cbcbcb;
}
.react-contextmenu-item:hover {
  cursor: pointer;
  -webkit-transition: 0.1s ease-out;
  -o-transition: 0.1s ease-out;
  transition: 0.1s ease-out;
  background-color: rgba(0, 0, 0, 0.08);
}
/* width */
::-webkit-scrollbar {
  width: 7px;
}
/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 3px grey;
          box-shadow: inset 0 0 3px grey;
  border-radius: 5px;
}
::-webkit-scrollbar-thumb {
  background: #a19f9f;
  border-radius: 5px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #757575;
}



